import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ScholarshipTestPage } from './scholarship-test.page';

const routes: Routes = [
  {
    path: '',
    component: ScholarshipTestPage
  },
  {
    path: 'test-result/:studentId/:assignQbResultId',
    loadChildren: () => import('./test-result/test-result.module').then( m => m.TestResultPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ScholarshipTestPageRoutingModule {}
